<template>
  <div class="courseQualityAnalysis" v-loading.body="bodyLoading">
    <crumb style="padding: 0 5px;"></crumb>
    <div class="mainContainer">
      <query-filter :showData="queryAuthority" @onSelect="querySelect"></query-filter>
      <div class="tableBody">
        <el-table
          :data="tableData"
          stripe
          style="width: 100%"
        >
          <el-table-column label="问卷名称" width="200"  :show-overflow-tooltip="true">
            <template slot-scope="scope">
              {{ scope.row.formName}}
            </template>
          </el-table-column>
          <el-table-column prop="ext2" label="调查年级"></el-table-column>
          <!-- <el-table-column prop="startTime" label="开始时间">
            <template slot-scope="scope">
              <span>{{ scope.row.startTime | time}}</span>
            </template>
          </el-table-column>
          <el-table-column prop="endTime" label="结束时间">
            <template slot-scope="scope">
              <span>{{ scope.row.endTime | time}}</span>
            </template>
          </el-table-column> -->
          <el-table-column prop="receiveNum" label="回收数量"></el-table-column>
          <el-table-column label="操作" width="240">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="handleEdit(scope.row)"
                v-havaRole="'courseQualityAnalysis'"
              >
                <SvgIcon icon-class="icondiaochafenxi"></SvgIcon>&nbsp;&nbsp;调查分析
              </el-button>
              <el-button
                type="text"
                size="small"
                @click="handleView(scope.row)"
                v-havaRole="'courseQualityAnalysisView'"
              >
                <SvgIcon icon-class="iconyulan"></SvgIcon>&nbsp;&nbsp;预览
              </el-button>
              <el-button
                type="text"
                size="small"
                @click="handleStart(scope.row)"
                v-havaRole="'courseQualityAnalysisStart'"
              >
                <SvgIcon icon-class="iconyuanshidajuan"></SvgIcon>&nbsp;&nbsp;原始答卷
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="pagination">
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="form.page"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="form.pageSize"
             layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryFilter from "../components/QueryFilter.vue";
import { formatDate } from "@/utils/utils";
import Crumb from '@/components/Crumb'

export default {
  components: {
    QueryFilter,
    Crumb
  },
  data() {
    return {
      form: {
        busiGroup3: "",
        ext1: "",
        ext2: "",
        ext3: "",
        page: 1,
        pageSize: 15
      },
      tableData: [],
      total: 0,
      bodyLoading: true,
      roleName: localStorage.getItem("roleName"),
      queryAuthority: [], //筛选条件
      api: {
        listEndCourseQuality:
          "/form/rest/FormMainBaseBusiness/listEndCourseQuality/v1"
      }
    };
  },
  created() {
    if (this.roleName === "学院管理员" || this.roleName === "教师") {
      this.queryAuthority = [1, 2, 4, 5];
    } else if (
      this.roleName === "专业管理员" ||
      this.roleName === "课程负责人"
    ) {
      this.queryAuthority = [2, 4, 5];
    }
  },
  mounted() {
    this.getList();
  },
  methods: {
    querySelect(res) {
      this.form.busiGroup3 = res.specialtyId;
      this.form.ext1 = res.versionId;
      this.form.ext2 = res.classId;
      this.form.ext3 = res.schedulingGradesId;
      this.form.page = 1;
      this.form.pageSize = 15;
      this.getList();
    },
    getList() {
      this.$post(this.api.listEndCourseQuality, this.form).then(res => {
        this.bodyLoading = false;
        if (res.code === 200) {
          this.tableData = res.data.rows;
          this.total = res.data.total;
        }
      });
    },
    handleAdd() {},
    handleEdit(row) {
      this.$router.push({
        path: "/courseQualityEvaluation/courseNewifon",
        query: {
          id: row.id,
          title: row.formName
        }
      });
    },
    handleView(row) {
      this.$router.push({
        path: "/evaluationAndFeedback/questionnairePreview",
        query: {
          entryType: "rest",
          id: row.id
        }
      });
    },
   handleStart(row) {
        this.$router.push({
            path: '/courseQualityEvaluation/originalpage',
            query:{
                id:row.id
                }
        })
    },
    handleSizeChange(val) {
      this.form.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.form.page = val;
      this.getList();
    }
  },
  filters: {
    time(val) {
      return val ? formatDate(val, "yyyy-MM-dd") : "";
    }
  }
};
</script>

<style lang="scss">
.courseQualityAnalysis {
  text-align: left;
  padding: 0 0 20px;
  .mainContainer {
    overflow: hidden;
    margin-bottom: 87px;
    .title {
      line-height: 40px;
      margin-bottom: 20px;
      span {
        font-size: 14px;
        font-weight: 700;
        color: #666;
      }
      .buttonGroup {
        margin-bottom: 10px;
        overflow: hidden;
        line-height: 32px;
        float: right;
      }
    }
    .listQuery {
      background-color: #fff;
      padding: 22px 0 0;
      border-radius: 10px;
      margin: 0 5px 20px;
    }
    .tableBody {
      overflow: initial;
      border-radius: 10px 10px 0 0;
      margin: 0 5px;
      .el-table {
        .distance {
          margin-right: 10px;
        }
        .el-table__header {
          tr {
            height: 50px;
            th {
              background: #fff;
              .cell {
                color: #666;
                font-weight: 700;
              }
            }
          }
        }
        .el-table__body {
          .el-table__row {
            height: 40px;
            td {
              padding: 0;
            }
          }
        }
      }
    }
    .pagination {
      padding: 20px;
      margin: 0 5px;
      text-align: right;
      background-color: #fff;
      border-radius: 0 0 10px 10px;
    }
  }
}
</style>



